.breadcrumb {
	color: $white;
	font-size: 12px;

	li {
		display: inline-block;

		&:not(:last-child) {
			/* padding-right: 20px; */

			&::after {
				content: '\2666';
				margin: 0 20px;
			}
		}

		a {
			color: $white;

			&:hover {
				color: $color-primary;
			}
		}
	}
}